@import url(https://fonts.googleapis.com/css2?family=Inconsolata&family=Roboto&display=swap);
/*
  Disables the knob's value label
  Unfortunately there's no better way to do this with this library :-/
*/
.knob-no-value > div > div > div :nth-child(2) {
  font-size: 0px !important;
}
body {
  margin: 0;
  font-family: 'Inconsolata';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root,
body,
html {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
}

